            max-width: 650px;
            margin-inline: auto; 
            background: #fff;
            padding: 8px 16px;
            border-radius: 12px;
        }

        &-heading{
            display: flex;
            align-items: center;
            justify-content: space-between;
            border-bottom: 1px solid rgb(0 0 0 / 10%);
            padding-bottom: 10px;
        }

        &-body{ 
            height: fit-content;
            padding: 16px 0;

            & form > .form-group > label{
                font-size: 14px;
                color: #777;
                font-weight: bold;
            }

            & form > .form-group > input.form-control{
                padding: 10px 12px;
                border: 1px solid rgb(0 0 0 / 10%);
                font-size: 14px;
            }
        }
    }
}


// single task stop button controller
.sp1_conf--button-group{
    display: flex;
    justify-content: center;
    align-items: stretch;
    gap: 10px;
    margin-top: 10px;

    & > button{ 
        padding: 3px 16px;
        border-radius: 6px;
        font-size: 13px;
        line-height: 14px;
        color: #fff;
        transition: background 0.2s ease-in-out;

        &:first-child{
            font-size: 1rem; 
            background-color: var(--header_color);
            &:hover{
                color: #fff;
                background: #444958;
            }
        } 
        &:nth-child(2){
            background-color: #444958;
            &:hover{
                color: #fff;
                background: #1e6cff;
            }
        }  
    }
}

// single task page
.sp1_single_task--modal{
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 99999;
    width: 100vw;
    height: 100vh;
    overflow: hidden; 
}

.sp1_single_task--modal-panerl-wrapper{
    display: block;
    width: 100vw;
    height: 100vh;
    overflow-y: auto;
    padding: 20px;
    background:rgba(24, 27, 32, 0.089);
}

.sp1_single_task--modal-panel{
    width: fit-content;
    max-width: 90vw;
    height: fit-content; 
    background: #fff;
    padding: 10px;
    border-radius: 10px;
    margin: auto;
}

.sp1_single_task--modal-body{
    height: 100%;
}


.sp1_stop-button-confirmation-option{
    h6{ font-weight: bold; }
}

.task-selection-list-option{
    padding: 8px 10px;
    margin-bottom: 2px;
    border-radius: 3px;
    display: grid;
    grid-template-columns: 1fr 24px;
    border-bottom: 1px dashed rgb(0 0 0 / 10%);
    &:last-child{
        border: none;
    }

    &.selected{
        background-color: rgb(24, 96, 240);
        color: #fff;
    }

    &.active{
        background-color: rgba(24, 96, 240, 0.3);
        color: #000;
    }
}



.mark-as-compeleted-link{
    display: flex;
    align-items: center;
    position: relative;
}

.mark-as-compeleted-link button{
    position: absolute;
    top: 50%;
    right: 10px;
    display: none;
    background-color: transparent;
    color: rgb(88, 77, 77);
    padding: 4px;
    transform: translateY(-50%);
}


.mark-as-compeleted-link:hover button{
    color: #f51b1b;
    display: block;
}

.sp1_remove-time-duration{
    background: rgb(250, 226, 226);
    color: #f00;
    height: 100%;
    width: 42px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 6px;
    transition: all 0.4 ease-in-out;

    &:hover{
        background: #f00;
        color: #fff;
    }
}


/* revision button */
.sp1-st-revision-btn{
    background: rgb(240, 17, 17) !important;
    color: #fff !important;
    border-color: rgb(240, 17, 17) !important;
    cursor: pointer !important;

    &:hover{
        background: rgb(224, 4, 4) !important;
        color: #fff !important;
        border-color: rgb(224, 4, 4) !important;
    }

    &.--view-revision{
        background: rgb(250, 234, 234) !important;
        color: red !important;
        border-color: rgb(247, 214, 214) !important;

        &:hover{
            background: rgb(240, 17, 17) !important;
            color: #fff !important;
            border-color: red !important;
        }
    }
}

/* end revision button */

/* revision commet view */
.st_revision_comment{
    border: 1px solid rgb(0 0 0 / 5%);
    padding: 16px;
    border-radius: 6px;
}
/* end revision comment */

/* approve */
.sp1_st--approve-card{
    border: 1px solid rgb(0 0 0 / 10%); 

    &-header{
        display: flex;
        align-items: center;
        justify-content: space-between;
        font-size: 14px;
        font-weight: 500;
        padding: 5px 10px;
        background-color: rgb(238, 238, 240);
    }

    &-body{
        padding: 6px 10px;
    }
}


.task-report-form--modal-panel{
    width: 550px;
}

@media only screen and (max-width:632px){
    .task-report-form--modal-panel{
        width: 100%;
    }
}

// report button
.sp1_report_btn{
    @media (max-width: 490px ) {
        margin-left: 0 !important ; 
    }            
}
 
.sp1_task-edit-button{
    margin-left: auto;
    &:hover{
        color: #fff !important;
        background-color: #084bc7 !important;
    }

    @media (max-width: 490px ) {
        margin-left: 0 !important ; 
    }        
}